	import uniLoadMore from "@/components/uni-load-more.vue"
	export default {
		components: {
			uniLoadMore
		},
		data() {
			return {
				tabBars: [],
				scrollLeft: 0,
				tabIndex: 0,
				symbolId: '',//分类id
				lists:[],
				loadingType: 0,
				next : -1,
				start: 0,
				size: 15,
				record: false,
				bottom: false,
				
			};
		},
		onLoad() {
			
		},
		onShow() {
           this.start = 0
		   console.log('-------------------------------')
		   console.log(this.tabIndex)
		   console.log('-------------------------------')
		   this.requestServe();
		},
		methods:{
			getElSize(id) { //得到元素的size
				console.log(id)
				return new Promise((res, rej) => {
					uni.createSelectorQuery().select("#" + id).fields({
						size: true,
						scrollOffset: true
					}, (data) => {
						res(data)
					}).exec()
				})
			},
			async tapTab(e) { //点击tab-bar
				this.symbolId = e.currentTarget.id
				console.log(this.symbolId)
				if (this.tabIndex === e.target.dataset.current) {
					return false;
				} else {
					let tabBar = await this.getElSize("tab-bar"),
					tabBarScrollLeft = tabBar.scrollLeft; //点击的时候记录并设置scrollLeft
					this.scrollLeft = tabBarScrollLeft;
					this.isClickChange = true;
					this.tabIndex = e.target.dataset.current;
				}
				this.start = 0
				this.requestServeList()
			},
			// 请求服务分类
			requestServe: function() {
				var that = this;
				uni.request({
					url: this.http+'service/category',
					method: 'GET',
					header: {
						authKey: this.authKey
					},
					data: {},
					success: res => {
						console.log(res.data)
						if (res.data.code == 200) {
							that.tabBars = res.data.data
							if (!that.symbolId){
								that.symbolId = res.data.data[0].id
							}
							this.requestServeList()
						}
					},
				});
			},
			// 请求服务分类的列表
			requestServeList: function(isLoading = true) {
				if (isLoading)	uni.showLoading({title: '加载中'})
				var that = this;
				let querys = "?start=" + this.start + "&size=" + this.size 
				uni.request({
					url: this.http+'service/list' + querys,
					method: 'GET',
					header: {
						authKey: this.authKey
					},
					data: {
						category_id:that.symbolId
					},
					success: res => {
						let that = this
						let data = res.data.data
						// console.log(res.data)
						if (res.data.code == 200) {
							// that.lists = res.data.data.list
							console.log(res.data.data.list)
							let order = data.list
							order.forEach(function(item){
								item.cover = that.static + item.cover
							})
							
							if (that.start == 0) 
								that.lists = []
						    that.start = data.next	
							that.lists = that.lists.concat(order)
							if (that.lists.length == 0) 
								that.record = true
							else 
								that.record = false
							if (order.length == 0) {
								that.loadingType = 2
							}
							else if (order.length < that.size || that.start == -1)
								that.loadingType = 2
							else 
								that.loadingType = 0
						}
					},
					fail: () => {
						console.log("请求失败")
					},
					complete: () => {
						if(isLoading) uni.hideLoading()
					}
				});
			},
			// 进入详情页
			goDetail(id){
